import styled from "styled-components";



export const WeatherIcons = {
    "01d": "/WeatherApp-React/icons/sunny.svg",
    "01n": "/WeatherApp-React/icons/night.svg",
    "02d": "/WeatherApp-React/icons/day.svg",
    "02n": "/WeatherApp-React/icons/cloudy-night.svg",
    "03d": "/WeatherApp-React/icons/cloudy.svg",
    "03n": "/WeatherApp-React/icons/cloudy-night.svg",
    "04d": "/WeatherApp-React/icons/perfect-day.svg",
    "04n": "/WeatherApp-React/icons/perfect-night.svg",
    "09d": "/WeatherApp-React/icons/rain.svg",
    "09n": "/WeatherApp-React/icons/rain-night.svg",
    "10d": "/WeatherApp-React/icons/rain.svg",
    "10n": "/WeatherApp-React/icons/rain-night.svg",
    "11d": "/WeatherApp-React/icons/storm.svg",
    "11n": "/WeatherApp-React/icons/storm.svg",
    "50d": "/WeatherApp-React/icons/mist.svg",
    "50n": "/WeatherApp-React/icons/mist.svg",
    "13d": "/WeatherApp-React/icons/snow.svg",
    "13n": "/WeatherApp-React/icons/snow.svg",
  };

const WeatherCondition = styled.div`
display:flex;
flex-direction:row;
align-items:center;
width:100%;
justify-content:space-between;
margin:30px auto;
`;
const Condition = styled.span`
${'' /* display:flex;
flex-direction:row; */}
font-size:16px;
margin:5px auto;
& span{
    font-size:28px;
}
`
const WeatherLogo = styled.img`
width:100px;
height: 100px;
margin: 10px auto;

`;
const Location = styled.span`
font-size:28px;
font-weight:bold;

`
const WeatherInfo = styled.span`
font-size:16px;
font-weight:bold;
text-align:start;
width:90%;
margin:20px 25px 10px;
`
const WeatherInfoContainer = styled.div`
  display: flex;
  width: 90%;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
`;
const InfoContainer = styled.div`
  display: flex;
  margin: 5px 10px;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
`;
const InfoIcon = styled.img`
  width: 36px;
  height: 36px;
`;
const InfoLabel = styled.span`
  display: flex;
  flex-direction: column;
  font-size: 14px;
  margin: 15px;
  & span {
    font-size: 12px;
    text-transform: capitalize;
  }
`;
function WeatherInfoComponent(props){
    return(<>
        <InfoContainer>
            <InfoIcon src={props.icon}/>
            <InfoLabel>
            {props.value}
            <span>{props.title}</span>

            </InfoLabel>
        </InfoContainer>
    </>)
}
function WeatherComponent(props){
    const {weather}=props;
    const isDay = weather?.weather[0].icon?.includes('d')
    // const getTime = (timeStamp) => {
    //     return `${new Date(timeStamp * 1000).getHours()} : ${new Date(timeStamp * 1000).getMinutes()}`
    // }
    const sunriseTime = new Date(weather?.sys?.sunrise * 1000).toLocaleTimeString();
    const sunsetTime = new Date(weather?.sys?.sunset * 1000).toLocaleTimeString()
    return (<>
        <WeatherCondition>
            <Condition>
            <span>{Math.floor(weather?.main?.temp - 273)+" Â°c"}</span> | {weather?.weather[0]?.description}
            </Condition>
            <WeatherLogo src={WeatherIcons[weather?.weather[0].icon]}/>
        </WeatherCondition>
        <Location>{weather?.name},{weather?.sys?.country}</Location>
        <WeatherInfo>Weather Info</WeatherInfo>
        <WeatherInfoContainer>
            <WeatherInfoComponent title={isDay ? "sunset" : "sunrise"} 
            value={isDay ? sunsetTime:sunriseTime} 
            icon="/WeatherApp-React/icons/temp.svg"/>
            <WeatherInfoComponent title="Humidity" value={weather?.main?.humidity+" %"} icon="/WeatherApp-React/icons/humidity.svg"/>
            <WeatherInfoComponent title="Wind" value={weather?.wind?.speed+" m/s"} icon="/WeatherApp-React/icons/wind.svg"/>
            <WeatherInfoComponent title="Pressure" value={weather?.main?.pressure+" hpa"} icon="/WeatherApp-React/icons/pressure.svg"/>
        </WeatherInfoContainer>
    </>)
}

export default WeatherComponent;