import styled from "styled-components";

// Styling Section.........
const WeatherLogo = styled.img`
width:140px;
height: 140px;
margin: 40px auto;

`;
const CityLabel = styled.span`
display:flex;
flex-direction:row;
align:center;
font-size:18px;
font-weight:bold;
margin:10px auto;
color:black;
`;
const Searchbox = styled.form`
display:flex;
flex-direction:row;
margin:20px auto;
border: solid black 1px;
border-radius:2px;
& input{
    padding:10px;
    font-size:14px;
    border:none;
    outline:none;
    font-weight:bold;
}
& button{
    padding:10px;
    font-size:14px;
    border:none;
    color:white;
    background-color:black;
    outline:none;
    font-weight:bold;
    cursor:pointer;
}
`;

//Implementation.....
function CityComponent(props){

    return(
    <>
     <WeatherLogo src="/WeatherApp-React/icons/perfect-day.svg"/>
     <CityLabel>Enter Your City Name</CityLabel>
     <Searchbox onSubmit={props.getWeather}>
     <input onChange={(event)=>props.onAdd(event.target.value)} 
     type="text" placeholder="City name">
     </input>
     <button type="submit" value="submit">Search</button>
     </Searchbox>
     </>
    );
}

export default CityComponent;