import Axios from "axios";
import { useState } from 'react';
import styled from 'styled-components'
import CityComponent from './components/CityComponent';
import WeatherComponent from './components/WeatherComponent';



//Styling Section.......
const Container = styled.div`
display: flex;
flex-direction: column;
margin: auto;
align-items: center;
box-shadow: 0 3px 6px 0 #555;
padding: 20px 10px;
border-radius: 4px;
width: 380px;
background: white;
font-family:Montserrat;

`;
const AppLabel = styled.span`
font-size:18px;
font-weight:bold;
color:black;
`;

//Main Code.........
// const API_KEY="48b8ca4dc04bd3eddd8e80771a8d7e63";
function App() {
  const [city,updateCity] = useState();
  const [weather,updateWeather] = useState();

  const fetchWeather = async(event) => {
    event.preventDefault();
    const response = await Axios.get(
      `https://api.openweathermap.org/data/2.5/weather?q=${city}&appid=48b8ca4dc04bd3eddd8e80771a8d7e63`,
    );
    updateWeather(response.data);
  }
  return (
    <Container >
      <AppLabel>The Weather App</AppLabel>
      {weather ? <WeatherComponent weather={weather}/>
      : <CityComponent onAdd={updateCity} getWeather={fetchWeather}/>}
    </Container>
  );
}

export default App;
